export const replaceLetters = (text) => {
  return text
    .slice(12)
    .replaceAll(' ', '')
    .toLowerCase()
    .replaceAll('', 'a')
    .replaceAll('', 'c')
    .replaceAll('', 'e')
    .replaceAll('', 'l')
    .replaceAll('', 'n')
    .replaceAll('', 'o')
    .replaceAll('', 's')
    .replaceAll('', 'z')
    .replaceAll('', 'z');
};
